import { Fragment } from "react"
import { styled } from "@rp/react/web/mui/core"
import { CqsQueryBarContext } from "../cqs-query-bar.types"
import { BaseChip } from "./BaseChip"
import { QueryChip } from "./QueryChip"

type OperationChipProps = {
  operation: string
  filters: Array<string> | null
  onRemove?: () => void
  query: CqsQueryBarContext
}

const BaseChipStyle = styled(BaseChip)(({ theme }) => ({
  margin: "3px",
  border: `1px solid ${theme.palette.text.disabled}`,
  backgroundColor: "transparent",
  height: "auto",
  flexBasis: 500,
  borderRadius: "32px",
  color: theme.palette.text.secondary,
  ":hover": {
    backgroundColor: "transparent !important",
  },
  "& .MuiChip-label": {
    display: "flex",
    flexGrow: 1,
  },
}))

const OperationChip = ({ operation, filters, onRemove, query }: OperationChipProps) => {
  if (!filters) return null
  if (filters.length === 0) return null

  return (
    <BaseChipStyle onRemove={onRemove} operation={operation}>
      {filters.map(filterId => {
        const filter = query.actions.expression.getFilter(filterId)
        if (filter === null) {
          return null
        }
        return (
          <Fragment key={filterId}>
            <span style={{ marginLeft: "2px" }} />
            <QueryChip query={query} filter={filter} />
            <span style={{ marginRight: "2px" }} />
          </Fragment>
        )
      })}
    </BaseChipStyle>
  )
}

export { OperationChip }
export type { OperationChipProps }
